import React from 'react';
import { BrowserRouter as Router, Route, Routes } from 'react-router-dom';
import Navbar from './components/navbar'; 
import Slides from './components/slides'; 
import 'bootstrap/dist/css/bootstrap.min.css';
import TrainAndHire from './components/TrainAndHire';
import Logos from './components/Logos';
import Services from './components/Services';
import ContactUs from './components/contactus';
import AboutUs from './components/aboutus';
import LoaderAndWhatsApp from './components/LoaderAndWhatsApp';
import Gallery from './Gallery/Gallery';
import './theme.css';

function App() {
  return (
    <Router>
      <Navbar />
      <Routes>
        <Route path="/" exact element={<section className="homepage" id="home">
          <Slides />
          <Logos />
        </section>} />
        <Route path="/train-and-hire" element={<TrainAndHire />} />
        <Route path="/services" element={<Services />} />
        <Route path="/about-us" element={<AboutUs />} />
        <Route path="/contact-us" element={<ContactUs />} />
        <Route path="/gallery" element={<Gallery />} />
      </Routes>
      <LoaderAndWhatsApp />
    </Router>
  );
}

export default App;
