import React from 'react';

const containerStyle = {
  backgroundColor: '#f8f9fa',
  padding: '20px',
  borderRadius: '10px',
  textAlign: 'center',
  margin: '20px auto',
  maxWidth: '800px',
};

const titleStyle = {
  fontSize: '24px',
  fontWeight: 'bold',
  marginBottom: '10px',
};

const descriptionStyle = {
  fontSize: '16px',
  fontStyle: 'italic',
  color: '#6c757d',
};

const imageStyle = {
  width: '50px',
  height: '50px',
  borderRadius: '50%',
  marginRight: '10px',
};

const flexContainerStyle = {
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
};

const con1Style = {
  width: '100%',
  padding: '45px',
  background: "url('../src_pic/pexels-photo-5313161.jpeg') no-repeat center center",
  backgroundSize: 'cover',
  border: '5px solid',
  animation: 'borderFlash 10s infinite',
};

const borderFlashKeyframes = `
  @keyframes borderFlash {
    0% { border-color: #ff9a9e; }
    50% { border-color: #150fc9; }
    100% { border-color: #e70911; }
  }
`;

const courseCardStyle = {
  borderRadius: '10px',
  overflow: 'hidden',
  marginBottom: '20px',
  transition: 'transform 0.3s ease',
  borderColor: 'aquamarine',
  size: '20px',
};

const courseCardHoverStyle = {
  transform: 'scale(1.05)',
};

const courseTitleStyle = {
  fontSize: '2rem',
  fontWeight: 'bold',
  marginBottom: '30px',
  color: 'black',
  textAlign: 'center',
};

const cardBodyStyle = {
  textAlign: 'center',
  backgroundColor: 'rgba(255, 255, 255, 0.8)',
};

const cardBodyTitleStyle = {
  color: '#333',
};

const courses = [
  "Full Stack",
  "Front end Full Stack",
  "Back end Full Stack",
  "My SQL",
  "Data Science",
  "Python",
  "Data Analyst",
  "Pro Power Bi",
  "Cyber Security",
  "Business Communications",
  "Human Resource Analyst"
];

const TrainAndHire = () => {
  return (
    <div className="Company" style={{ textAlign: 'center', paddingTop: '40px' }}>
      <div className="container con1" style={con1Style}>
        <style>{borderFlashKeyframes}</style>
        <div className="course-title" style={courseTitleStyle}>Train and Hire</div>
        <div className="row">
          {courses.map((course, index) => (
            <div className="col-md-4" key={index}>
              <div
                className="card course1-card"
                style={courseCardStyle}
                onMouseOver={(e) => (e.currentTarget.style.transform = courseCardHoverStyle.transform)}
                onMouseOut={(e) => (e.currentTarget.style.transform = 'none')}
              >
                <div className="card-body" style={cardBodyStyle}>
                  <h5 className="card-title" style={cardBodyTitleStyle}>{course}</h5>
                </div>
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default TrainAndHire;
