import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faEnvelope, faPhone, faClock, faUser, faMessage } from '@fortawesome/free-solid-svg-icons';

const ContactUs = () => {
  const sectionStyle = {
    // backgroundColor: '#f1f1f1', // Light grey background
    padding: '3rem 0',
    borderRadius: '30px',
  };

  const overlayStyle = {
    // backgroundColor: 'rgba(255, 255, 255, 0.8)', // White overlay with opacity
    borderRadius: '10px',
    padding: '3rem',
  };

  const cardStyle = {
    backgroundColor: '#808080', // Gray background for the cards
    border: '1px solid #dee2e6', // Light border
    borderRadius: '10px',
    padding: '2rem',
    boxShadow: '0 4px 12px rgba(0, 0, 0, 0.1)',
    marginBottom: '2rem',
    color: 'white',
    fontSize: '16px', // Font size for card content
  };

  const formCardStyle = {
    ...cardStyle,
  };

  const inputGroupStyle = {
    display: 'flex',
    alignItems: 'center',
    marginBottom: '1rem',
  };

  const inputIconStyle = {
    // backgroundColor: '#f1f1f1', // Light grey
    border: '1px solid #ced4da',
    borderRight: 'none',
    padding: '0.75rem',
    borderRadius: '5px 0 0 5px',
  };

  const inputStyle = {
    // backgroundColor: '#f1f1f1', // Light grey input fields
    color: '#333',
    border: '1px solid #ced4da', // Subtle border
    padding: '0.75rem',
    borderRadius: '0 5px 5px 0',
    width: '100%',
    fontSize: '16px', // Font size for input fields
  };

  const textareaStyle = {
    ...inputStyle,
    borderRadius: '5px',
    height: '150px',
  };

  const buttonStyle = {
    backgroundColor: '#007bff', // Primary blue color
    color: 'white',
    border: 'none',
    padding: '0.75rem 1.5rem',
    borderRadius: '5px',
    cursor: 'pointer',
    width: '100%',
    fontWeight: 'bold',
    fontSize: '16px', // Font size for button
    transition: 'background-color 0.3s ease',
  };

  const headingStyle = {
    color: 'gold', // Gold color for headings
    fontWeight: 'bold',
    fontSize: '24px', // Font size for headings
    marginBottom: '1.5rem',
  };

  const textMutedStyle = {
    // color: '#fff',
    marginBottom: '0.5rem',
    fontSize: '16px', // Font size for muted text
  };

  return (
    <div className="container" style={sectionStyle}>
      <div style={overlayStyle}>
        <h1 style={{ textAlign: 'center', color: 'gold', paddingBottom: '2rem', fontSize: '32px' }}>Contact Us</h1> {/* Gold color and larger font size for the Contact Us heading */}
        <div className="row">
          <div className="col-lg-6">
            <div style={cardStyle}>
              <h4 style={headingStyle}>Our Location</h4>
              <p style={textMutedStyle}>
                <FontAwesomeIcon icon={faEnvelope} /> pagoachievers.com
              </p>
              <p style={textMutedStyle}>
                <FontAwesomeIcon icon={faPhone} /> +91 9840369759
              </p>
            </div>
            <div style={cardStyle}>
              <h4 style={headingStyle}>Business Hours</h4>
              <ul className="list-unstyled">
                <li style={{ fontSize: '16px' }}>
                  <FontAwesomeIcon icon={faClock} /> Monday - Friday: 8:00 AM - 8:00 PM
                </li>
                <li style={{ fontSize: '16px' }}>
                  <FontAwesomeIcon icon={faClock} /> Saturday: 9:00 AM - 5:00 PM
                </li>
                <li style={{ color: '#dc3545', fontSize: '16px' }}>
                  <FontAwesomeIcon icon={faClock} /> Sunday: Closed
                </li>
              </ul>
            </div>
          </div>
          <div className="col-lg-6">
            <div style={formCardStyle}>
              <h4 style={headingStyle}>Get in Touch</h4>
              <form>
                <div style={inputGroupStyle}>
                  <span style={inputIconStyle}>
                    <FontAwesomeIcon icon={faUser} />
                  </span>
                  <input type="text" style={inputStyle} placeholder="Your Name" />
                </div>
                <div style={inputGroupStyle}>
                  <span style={inputIconStyle}>
                    <FontAwesomeIcon icon={faEnvelope} />
                  </span>
                  <input type="email" style={inputStyle} placeholder="Your Email" />
                </div>
                <div className="form-group">
                  <textarea className="form-control" style={textareaStyle} placeholder="Your Message"></textarea>
                </div>
                <button
                  type="submit"
                  style={buttonStyle}
                  onMouseOver={(e) => (e.target.style.backgroundColor = '#0056b3')}
                  onMouseOut={(e) => (e.target.style.backgroundColor = '#007bff')}
                >
                  <FontAwesomeIcon icon={faMessage} /> Send Message
                </button>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ContactUs;
