
import React, { useEffect, useState } from 'react';

const LoaderAndWhatsApp = ({ children }) => {
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    // Simulate loading time (e.g., 3 seconds)
    const timer = setTimeout(() => {
      setIsLoading(false);
      // Optionally scroll to the top of the page
      window.scrollTo(0, 0);
    }, 3000); // 3000ms = 3 seconds

    return () => clearTimeout(timer); // Cleanup the timer on unmount
  }, []);

  return (
    <>
      {isLoading ? (
        <div className="loader">
          <div></div>
        </div>
      ) : (
        <>
          <div id="home">
            {children}
          </div>
          <div id="whatsapp">
            <a href="https://wa.me/919840369759" target="_blank" id="toggle1" className="wtsapp">
              <img 
                src="https://uxwing.com/wp-content/themes/uxwing/download/brands-and-social-media/whatsapp-white-icon.png" 
                alt="WhatsApp" 
                className="wtsapp-icon" 
              />
            </a>
          </div>
        </>
      )}
      <style jsx>{`
        .loader {
          height: 100vh;
          width: 100vw;
          overflow: hidden;
          background-color: #16191e;
          position: fixed;
          top: 0;
          left: 0;
          display: flex;
          justify-content: center;
          align-items: center;
          z-index: 9999;
        }

        .loader > div {
          height: 100px;
          width: 100px;
          border: 15px solid #45474b;
          border-top-color: #2a88e6;
          border-radius: 50%;
          animation: spin 1.5s infinite linear;
        }

        @keyframes spin {
          100% {
            transform: rotate(360deg);
          }
        }

        #whatsapp {
          position: fixed;
          bottom: 10px;
          right: 18px;
          z-index: 9999;
        }

        #whatsapp .wtsapp {
          transform: all 0.5s ease;
          background-color: #25d366;
          display: flex;
          justify-content: center;
          align-items: center;
          box-shadow: 0 0 20px rgba(0, 0, 0, 0.15);
          border-radius: 50px;
          width: 60px;
          height: 60px;
        }

        .wtsapp-icon {
          width: 30px; /* Adjust size as needed */
          height: 30px;
        }

        #whatsapp .wtsapp:before {
          content: '';
          position: absolute;
          left: 50%;
          top: 50%;
          transform: translate(-50%, -50%);
          width: 60px;
          height: 60px;
          background-color: #25d366;
          border-radius: 50%;
          animation: pulse-border 1500ms ease-out infinite;
        }

        @keyframes pulse-border {
          0% {
            transform: translate(-50%, -50%) scale(1);
            opacity: 1;
          }
          100% {
            transform: translate(-50%, -50%) scale(1.5);
            opacity: 0;
          }
        }
      `}</style>
    </>
  );
};

export default LoaderAndWhatsApp;