const ServiceCard = ({ imgSrc, title, description }) => (
  <div className="col-md-4">
    <div className="card text-center h-100" style={styles.card}>
      <img src={imgSrc} className="card-img-top mx-auto" alt={title} style={styles.image} />
      <div className="card-body">
        <h5 className="card-title" style={styles.title}>{title}</h5>
        <p className="card-text" style={styles.description}>{description}</p>
      </div>
    </div>
  </div>
);

const OurServicesCard = () => {
  return (
    <div style={{
       backgroundImage: `url('https://img.freepik.com/free-vector/dark-black-background-design-with-stripes_1017-38064.jpg')`,
       backgroundSize: 'cover', // This will make the background image cover the entire div
       backgroundPosition: 'center', // This will center the background image
       backgroundColor: '#f9f9f9', // You can keep this if you want a fallback color
       padding: '20px',
       borderRadius: '10px',
       boxShadow: '0px 4px 6px rgba(0, 0, 0, 0.1)',
       display: 'flex',
       alignItems: 'center',
       justifyContent: 'center',
       margin: '20px 0',
    }}>
      <div style={{
        display: 'flex',
        alignItems: 'center'
      }}>
        <img 
          src="../services.jpg" 
          alt="Our Services" 
          style={{
            width: '80px',
            height: '80px',
            border:'3px solid #3b32e3',
            borderRadius: '50%',
            marginRight: '20px',
          }} 
        />
        <p style={{
          fontSize: '18px',
          color: '#fff',
          fontWeight: '500',
          lineHeight: '1.5',
          textAlign: 'center'
        }}>
          "Our mission: <strong>Empower minds, transform lives.</strong> Enriching participants with knowledge daily, ensuring each session is empowering and transformative."
        </p>
      </div>
    </div>
  );
}

const OurPortfolioCard = () => {
  return (
    <div style={{
       backgroundImage: `url('https://img.freepik.com/free-vector/dark-black-background-design-with-stripes_1017-38064.jpg')`,
       backgroundSize: 'cover', // This will make the background image cover the entire div
       backgroundPosition: 'center', // This will center the background image
       backgroundColor: '#f9f9f9', // You can keep this if you want a fallback color
       padding: '20px',
       borderRadius: '10px',
       boxShadow: '0px 4px 6px rgba(0, 0, 0, 0.1)',
       display: 'flex',
       alignItems: 'center',
       justifyContent: 'center',
       margin: '20px 0',
    }}>
      <div style={{
        display: 'flex',
        alignItems: 'center'
      }}>
        <img 
          src="../Empowerment.jpg"  
          alt="Our Portfolio" 
          style={{
            width: '80px',
            height: '80px',
            border:'3px solid #3b32e3',
            borderRadius: '50%',
            marginRight: '20px'
          }} 
        />
        <p style={{
          fontSize: '19px',
          color: '#fff',
          fontWeight: '500',
          lineHeight: '1.5',
          textAlign: 'center'
        }}>
          <strong>PAGO Achievers</strong> is a professionally managed organization specializing in "Training and Placement" services for the past four years. Founded with the objective of providing comprehensive global training and placement solutions to the aspiring individuals and the corporate sector, PAGO Achievers offers a range of Campus to Corporate training programs.
        </p>
      </div>
    </div>
  );
}

const Services = () => {
  return (
    <div className="container mt-5">
      
      {/* Our Services Card */}
      <OurServicesCard />

      {/* Services Grid */}
      <div className="row">
        {services.map((service, index) => (
          <div key={index} className="col-md-4 mb-4"
          style={{height: '400px'}}
          >
            <div className="card service-card h-100 text-center"
            style={{
              backgroundImage: 'url(https://img.freepik.com/free-vector/dark-black-background-design-with-stripes_1017-38064.jpg)', // Replace with your image URL
              backgroundSize: 'cover', // Adjusts the background image to cover the entire container
              backgroundPosition: 'center', // Centers the background image
              backgroundRepeat: 'no-repeat', // Ensures the background image doesn't repeat
              height: '100vh', // Optional: Adjusts the height to fit the viewport height
              backgroundAttachment: 'fixed' // Optional: Fixes the background image in place
            
            }}>
              <img src={service.imgSrc} alt={service.title} className="card-img-top" 
                style={{width: '250px', height: '200px', objectFit: 'cover',display: 'block', 
                  marginLeft: 'auto', 
                  marginRight: 'auto' ,paddingTop: '10px',border:'5px' }} />
              <div className="card-body">
                <h5 className="card-title" style={{ color: 'white' }}>{service.title}</h5>
                <p className="card-text" style={{ color: 'white' }}>{service.description}</p>
              </div>
            </div>
          </div>
        ))}
      </div>

      {/* Our Portfolio Card */}
      <OurPortfolioCard />

      {/* Additional Services Data */}
      <div className="row">
        {servicesData.map((service, index) => (
          <ServiceCard
            key={index}
            imgSrc={service.imgSrc}
            title={service.title}
            description={service.description}
          />
        ))}
      </div>
    </div>
  );
};

const styles = {
  card: {
    backgroundColor: '#0a1f44', // Dark blue background
    color: 'white', // Set the text color to white
    borderRadius: '10px',
    padding: '20px',
    boxShadow: '0px 4px 8px rgba(0, 0, 0, 0.1)',
  },
  image: {
    borderRadius: '50%', // Rounded image like in the screenshot
    height: '100px',
    width: '100px',
    objectFit: 'cover',
    marginBottom: '15px',
  },
  title: {
    fontWeight: 'bold',
    fontSize: '1.2rem',
    marginBottom: '15px',
    color: 'white', // Set the title text color to white
  },
  description: {
    fontSize: '1rem',
    lineHeight: '1.5',
    margin: '10px 0 0',
    textAlign: 'center',
    color: 'white', // Set the description text color to white
  },
};
export default Services;